#modal .dialog-bg {
    z-index: 102;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
}
  
#modal .dialog-wrapper {
    z-index: 103;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    position: fixed;
    display: flex;
}
  
#modal .dialog__content {
    margin: 20vh auto auto auto;
    background: white;
    border-radius: 10px;
    box-shadow: 2px 2px 20px #555555;
}

#modal .modalBlue {
    border-top: 5px solid #00a6f3;
}
#modal .modalOrange {
    border-top: 5px solid orange;
}

#modal .header {
    padding: 6px 12px;
    background: #e4e4e4;
    border-radius: 10px 10px 0 0;
    /* font-weight: bold; */
}

#modal .modal_flex {
    display: flex;
    align-items: center;
    min-height: 90px;
}

#modal .modal_flex img {
    margin-left: 20px;
}
  
#modal .message {
    padding: 20px;
    font-size: 1.3rem;
}
  
#modal .btns {
    padding: 11px 0 0 15px;
    display: flex;
    justify-content: flex-end;
    background: #e4e4e4;
    border-radius: 0 0 10px 10px;
}
  
#modal button {
    margin-right: 15px;
    margin-bottom: 11px;
}

