:root {
    --fontSet1: Helvetica;
    --fontSet2: Roboto;
    --fontSet3: 'Segoe UI';
    --fontSet4: "Segoe UI", Arial, system-ui,-apple-system;
    /*
    system-ui,-apple-system
    system-ui, -apple-system, "Segoe UI"
    Roboto, Segoe UI, Helvetica, Arial, sans-serif;
    Segoe UI, Helvetica, Arial, Roboto, sans-serif;    */
    --body-bg-rgb: 255,255,255;
    --primary-rgb: 13,110,253;
    --accent-rgb: 255,228,132;
    --violet-rgb: 112.520718,44.062154,249.437846;
    --pink-rgb: 214,51,132;
}

body {
    background: linear-gradient(180deg, rgba(var(--body-bg-rgb), 0.01), rgba(var(--body-bg-rgb), 1) 85%),radial-gradient(ellipse at top left, rgba(var(--primary-rgb), 0.5), transparent 50%),radial-gradient(ellipse at top right, rgba(var(--accent-rgb), 0.5), transparent 50%),radial-gradient(ellipse at center right, rgba(var(--violet-rgb), 0.5), transparent 50%),radial-gradient(ellipse at center left, rgba(var(--pink-rgb), 0.5), transparent 50%);
    /* background-image: linear-gradient(to right top, #05193744, #004d7a44, #00879344, #00bf7244, #a8eb1244); */
    /* background-image: linear-gradient(to right top, #072e691f, #0068ad1f, #00a2b81f, #00d7831f, #b0ff001f); */
    /* background: linear-gradient( 10deg, #006eff17, #00a2ff17, #01fb7117) no-repeat; */
}


