/* .block { display: block; } */

.tooltip {
    position: relative;
    display: block;
    /* display: inline-block; */
    /* border: 2px solid green; */
    max-width: 540px;
}

.tooltip .tooltiptext {
    display: inline-block;
    /* display: block; */
    position: absolute;
    z-index: 100;
    top: 150%;
    /* left: 50%; */
    right: 14%;
    /* visibility: hidden; */
    width: 200px;
    /* max-width: 240px; */
    background-color: red;
    box-shadow: 2px 2px 10px #7a7a7a;
    color: #ffffff;
    text-align: center;
    border-radius: 6px;
    /* padding: 5px 0; */
    padding: 5px 10px;
    /* margin-left: -60px; */
    /* margin-left: 30px;
    margin-top: -23px; */
}

.tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent red transparent;
}

